html, body {
  height: 100%;
  width: 100%;
}

.page-container {
  width: 100%;
  min-height: 100vh;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: translateX(100px);
  height: auto;  
}

.home {
  width: calc(100% - 100px);
  display: flex;
  justify-content: center;
  padding: 4rem;
  margin-left: 100px;
  height: auto; 
}

.content-wrapper {
  width: 100%;
  max-width: 800px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  left:150px;
  height: auto;
}

.about-me {
  position: relative;
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto; 
  top: 200px;

}

.about-me .PROFILE-PIC {
  background-color: var(--mainprimary-lighter);
  border-radius: 200px;
  height: 400px;
  width: 400px;
  position: relative;
  margin-left: calc(-25vw - 60px);
  margin-top: 75px;
  display: inline-block;
  transform: translateX(0);
  left: -400px;
}

.about-me .ellipse {
  background-color: var(--mainprimary-darker);
  border-radius: 200px;
  height: 390px;
  width: 390px;
  position: relative;
  left: 5px;
  top: 5px;
}

.about-me-text {
  color: var(--mainprimary); 
  font-family: var(--text-heading-1-font-family);
  font-size: var(--text-heading-1-font-size); 
  font-weight: var(--text-heading-1-font-weight);
  letter-spacing: var(--text-heading-1-letter-spacing);
  line-height: var(--text-heading-1-line-height);
  margin-bottom: var(--text-heading-1-margin-bottom);
  text-transform: var(--text-heading-1-text-transform);
  text-align: left;
  width: 100%;
  position: relative;
  top: -400px;
  left: 80px;
}

.about-me .i-am-an-engineering,
.ABOUT-ME .div p {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  width: 80%;
  top: -390px;
  left: 80px;
}
