/* Base layout */
.page-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    height: auto;
    background-color: black !important; /* Ensure black background */
    position: relative;
    z-index: 1;
  }
  
  .content-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem; /* Increased gap for better spacing */
    height: auto;
    padding: 2rem 1rem; /* Add padding for better layout */
    z-index: 2; /* Ensure content is above background */
  }
  
  /* Section common styles */
  section {
    width: 100%;
    max-width: 1200px; /* Maximum width for better readability */
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    margin: 0 auto;
    color: white; /* Ensure text is white for visibility */
    z-index: 3; /* Higher z-index than content-wrapper */
  }
  
  /* Home component */
  .home {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 2;
  }
  
  /* Ensure divs inside sections are properly centered */
  section > div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* Add responsive media queries */
  @media (max-width: 768px) {
    .content-wrapper {
      gap: 1.5rem;
      padding: 1.5rem 0.75rem;
    }
    
    section {
      padding: 0.75rem;
    }
  }
  