html, body {
    height: 100%;
    width: 100%;
  }

.RESUME {
  background-color: transparent;
  height: auto;
  position: relative;
  width: 1440px;
  left: -330px;
  top: 600px;
}

.RESUME .techImage {
  height: 225px!important;
  left: 100px !important;
  position: relative !important;
  top: 75px !important;
  width: 250px !important;
  margin: 2rem 0;
  margin-top: -3px;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.RESUME .downloadImage {
  height: 225px;
  left: 140px;
  position: relative;
  top: 75px;
  width: 225px;
  margin: 2rem 0;
  margin-top: -3px;
  transition: transform 0.3s ease, opacity 0.3s ease;
  cursor: pointer;
}

.RESUME .downloadImage:hover {
  transform: scale(1.1);
  opacity: 0.8;
}

.RESUME .downloadImage:active {
  transform: scale(0.95);
}

.RESUME .text-wrapper {
  color: var(--mainprimary);
  font-family: var(--text-heading-1-font-family);
  font-size: var(--text-heading-1-font-size);
  font-style: var(--text-heading-1-font-style);
  font-weight: var(--text-heading-1-font-weight);
  left: 600px;
  letter-spacing: var(--text-heading-1-letter-spacing);
  line-height: var(--text-heading-1-line-height);
  position: absolute;
  text-align: center;
  top: 30px;
  white-space: nowrap;
}

.RESUME .overlap-group {
  height: auto;
  left: 870px;
  position: relative;
  top: 141px;
  width: 474px;
}

.RESUME .TECH-RESUME {
